import React, { useEffect, useState } from "react";

const token =
  "0df0d027c6858bd4f4e959cac423067b7e88aa5e12a0ec8cc1ba00daa6013f37d4fa9dfe1a84856a36cbc1fca986503bebceab0dc7d55959d3299006db56048d17ec27ff8b59b4366ffc21a783ce6abf1f3f3fa6e233867f873b624929ab4c8e1a23b81a6762747a30c7402c8608679e070764cb1fdb554293fcdf2c7726457d";

const url = "https://social-backends.herokuapp.com/api/payment-details";
export default function DataDisplayer(props) {
  const [datas, setDatas] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      const response = await fetch(url, {
        headers: {
          Authorization: `Bearer ${token}`
        }
      });
      const json = await response.json();
      setDatas(json);
      console.log(json);
    };

    fetchData();
  }, [props.id]);

  if (datas) {
    return (
      <div>
        {datas.data.map((data, id) => {
          return (
            <div key={data.id}>
              <div className="card w-96 bg-primary text-primary-content">
                <div className="card-body ">
                  <h2 className="card-title">{data.attributes.hesapSahibi}</h2>
                  <div className="card-actions justify-center">
                    <button className="btn">{data.attributes.ibanNo}</button>
                  </div>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    );
  } else {
    return null;
  }
}
